import neilModal from '@/components/neil-modal/neil-modal.vue';
import navBar from "@/components/nav-bar";
import { mapState } from 'vuex';
import utils from '../utils/method.js'
export default {
	components: {navBar,neilModal},
	data() {
		return {
			showModal:false,
			modalTitle:"",
			modalCont:"",
			notice:[],
			info:null,
			promoteInfo:null,
			maxPrice:""
		}
	},
	computed:{
		...mapState(['hasLogin','config','isVip'])
	},
	async onPullDownRefresh() {
		uni.showLoading({
			title:"数据加载中..."
		})
		await this.judgeLogin();
		await this.getConfig();
		await this.getNotice();
		await this.getInfo();
		uni.stopPullDownRefresh();
		uni.hideLoading();
	},
	methods: {
		bindBtn(e){
			if(e == 'confirm'){
				this.showModal = false;
			}
		},
		judgeLogin(){
			 let _session = uni.getStorageSync('session');
			 let _userInfo = uni.getStorageSync('userInfo')
			 if(_session && _userInfo){
				utils.setSesion(_session,_userInfo)
			 }
		},
		async Promote(){
			if(this.isVip == 1){
				uni.navigateTo({
					url:"share"
				})
			}else{
				let _self = this;
				if(!this.promoteInfo){
					await _self.getPromoteInfo();
				}
				uni.showModal({
				    title: '提示',
				    content: '成为VIP爱心推广大使点击“我确认”，成为普通推广大使点击“去转发”',
					cancelText:'去转发',
					confirmText:'我确认',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:`join?showWd=-1&vipAmount=${_self.promoteInfo.vipAmount}&vipMaskNum=${_self.promoteInfo.vipMaskNum}`
				            })
				        } else if (res.cancel) {
				            uni.navigateTo({
				            	url:"share"
				            })
				        }
				    }
				});
			}
		},
		async onGotUserInfo(type,data) {
			let _self = this;
			uni.showLoading({
			    title: '请求中...'
			});
			let res1 = await utils.wxLogin();
			if(res1){
				let res2 = await this.$http({
					apiName:'userLogin',
					method:'POST',
					data:res1
				})
				try{
					utils.setSesion(res2,res1);
					await _self.getInvite();
					if(type == 1){
						_self.Promote()
					}
					if(type == 2){
						_self.toDetail(data)
					}
				}catch(e){}
			}else{
				uni.showToast({
					icon: 'none',
					title: "授权失败",
					duration: 1500
				});
			}
			uni.hideLoading()
		 },
		 async getInvite(){
			await this.$http({
				apiName:"userInfo",
			}).then(res => {
				this.$store.commit("setmyInviteCode",res.inviteCode);
				this.$store.commit("setIsVip",res.vip);
			}).catch(_=>{})
		 },
		 async getConfig(){
			let res = await this.$http({
				apiName:"config"
			})
			try{
				var _prices = [];
				for(let key in res.BIZ_PRICE_LADDER){
					_prices.push(res.BIZ_PRICE_LADDER[key])
				}
				this.maxPrice = Math.max(..._prices);
				res["maxPrice"] = this.maxPrice;
				this.$store.commit('setConfig',res);
			}catch(e){}
		 },
		 async getNotice(){
			let res = await this.$http({
				apiName:"notice"
			}) 
			try{
				this.notice = res
			}catch(e){}
		 },
		 async getInfo(){
			let res = await this.$http({
				apiName:"productInfo"
			}) 
			try{
				this.info = res;
				this.info.map(item => {
					item["reserveNum"] = utils.dealWan(item["reserveNum"])
				})
			}catch(e){}
		 },
		 async getPromoteInfo(){
		 	let res = await this.$http({
		 		apiName:"getPromoteInfo",
		 	})
		 	try{
		 		this.promoteInfo = res;
		 	}catch(e){}
		 },
		 showNotice(item){
			 this.modalTitle = item.title;
			 this.modalCont = item.content;
			 this.showModal = true;
		 },
		 toDetail(id){
			 uni.navigateTo({
			 	url:"/pages/detail?id=" + id
			 })
		 },
		 toPath(path){
			 uni.navigateTo({
			 	url:path
			 })
		 },
	},
	async onLoad(opt) {
		uni.showLoading({
			title:"数据加载中..."
		})
		await this.judgeLogin();
		await this.getConfig();
		await this.getNotice();
		await this.getInfo();
		if(this.hasLogin){
			this.getInvite();
			this.getPromoteInfo()
		}
		uni.hideLoading();
		if(opt.inviteCode){
			this.$store.commit('setinviteCode',opt.inviteCode)
		}
		if(opt.q){
			let q = decodeURIComponent(opt.q);
			let _i1 = q.split('?')[1];
			let _i2 = _i1.split('=')[1];
			this.$store.commit('setinviteCode',_i2)
		}
	}
}
